import { createContext, useState, useEffect } from "react";

export const MyLanguageContext = createContext();

export function MyLanguageContextProvider({ children }) {
  const langStorage = JSON.parse(localStorage.getItem("techsolIsEnglish"));

  const [language, setLanguage] = useState(langStorage);

  const toggle = () => {
    setLanguage(!language);
  };

  useEffect(() => {
    localStorage.setItem("techsolIsEnglish", language);
  }, [language]);

  return (
    <MyLanguageContext.Provider value={{ language, toggle }}>
      {children}
    </MyLanguageContext.Provider>
  );
}
