import React, { useContext } from "react";
import ShopDetails from "../components/ShopDetails";
import shopPhoto from "../assets/images/shop-photo.png";
import shopPhotoArabic from "../assets/images/shop-photo-arabic.svg";

import CustomerForm from "../components/CustomerForm";
import { MyLanguageContext } from "../context/LanguageContext";
import enLang from "../assets/files/lang-en.json";
import arLang from "../assets/files/lang-ar.json";

function Contact() {
  const { language } = useContext(MyLanguageContext);
  const myLang = language ? enLang : arLang;
  return (
    <>
      <div className={`contact-details  ${!language ? "c-d-arabic" : ""}`}>
        <div className="contact-grid-1">
          <div className={`requirements-grid ${!language ? "r-g-arabic" : ""}`}>
            <h5 id="requirement">{myLang.contact_requirement}</h5>
            <div id="requirement-desctiption">
              <p className={`description ${!language ? "d-arabic" : ""}`}>
                {myLang.contact_content1} <br />
                {myLang.contact_content2}
              </p>
            </div>
          </div>

          <div
            className={`requirements-contact ${!language ? "r-c-arabic" : ""}`}
          >
            <ShopDetails />
          </div>
        </div>

        <div
          className={`contact-grid-2  ${!language ? "c-g-2-arabic" : ""}`}
          dir={!language ? "rtl" : "ltr"}
        >
          <div>
            <p id="get-in-touch">{myLang.contact_touch}</p>
            <p id="get-in-touch-des">{myLang.contact_touchContent}</p>
          </div>

          <CustomerForm />
        </div>
      </div>
      <div
        className={`image-div ${!language ? "i-d-arabic" : ""} `}
        style={{
          backgroundImage: `url(${!language ? shopPhotoArabic : shopPhoto})`,
        }}
      />
    </>
  );
}

export default Contact;
