import { Link } from "react-router-dom";
import React, { useContext } from "react";
import ShopDetails from "../ShopDetails";
import { MyLanguageContext } from "../../context/LanguageContext";
import enLang from "../../assets/files/lang-en.json";
import arLang from "../../assets/files/lang-ar.json";
import LogoEn from "../../assets/images/techsol-logo.svg";
import LogoAr from "../../assets/images/techsol-logo-arabic.svg";

function Footer() {
  const { language } = useContext(MyLanguageContext);
  const myLang = language ? enLang : arLang;

  return (
    <footer className={`root-footer ${!language ? "rt-ftr-arabic" : ""} `}>
      {/* <footer className={`root-footer`}> */}
      <div className={`top-grid-1 ${!language ? "t-g-1-arabic" : ""}`}>
        <img
          src={language ? LogoEn : LogoAr}
          className="footer-logo"
          alt="logo"
        />
        <div className="social-medial-links">
          <i className="fa-brands fa-facebook fa-xl"></i>
          <i className="fa-brands fa-instagram fa-xl"></i>
          <i className="fa-brands fa-linkedin-in fa-xl"></i>
        </div>
        <p className="copy-rights">{myLang.footer_copyrights}</p>
      </div>

      <div className={`top-grid-2 ${!language ? "t-g-2-arabic" : ""}`}>
        <span className="g2-heading">{myLang.footer_quickLinks}</span>
        <span>
          <Link to="/" className="links">
            {myLang.footer_story}
          </Link>
        </span>
        <span>
          <Link to="contact" className="links">
            {myLang.footer_contactUs}
          </Link>
        </span>
      </div>

      <div className={`top-grid-3 ${!language ? "t-g-3-arabic" : ""}`}>
        <span id="get-in-touch-2">{myLang.footer_touch}</span>
        <ShopDetails />
      </div>
    </footer>
  );
}

export default Footer;
