import { useContext } from "react";
import { MyLanguageContext } from "../context/LanguageContext";

function Provider({ source, heading, content }) {
  const { language } = useContext(MyLanguageContext);
  return (
    <div className={`s-container ${!language ? "s-c-arabic" : ""} `}>
      <img src={source} className="s-c-badge" alt="badge" />
      <div className={`s-c-text ${!language ? "s-c-t-arabic" : ""} `}>
        <p className="s-c-head">{heading}</p>
        <p className="s-c-content">{content}</p>
      </div>
    </div>
  );
}

export default Provider;
