import { useContext } from "react";
import Quality from "./Quality";
import { MyLanguageContext } from "../context/LanguageContext";
import enLang from "../assets/files/lang-en.json";
import arLang from "../assets/files/lang-ar.json";

// assets
import visionImg from "../assets/images/vision-group-19.png";
import circle1 from "../assets/images/circle-1.svg";
import circle2 from "../assets/images/circle-2.svg";
import circle3 from "../assets/images/circle-3.svg";
import circle4 from "../assets/images/circle-4.svg";
import circle5 from "../assets/images/circle-5.svg";

function Vision() {
  const { language } = useContext(MyLanguageContext);
  const myLang = language ? enLang : arLang;
  return (
    <div className={`vision ${!language ? "v-arabic" : ""}`}>
      <div className={`vision-div-1 ${!language ? "v-d-1-arabic" : ""}`}>
        <div className={`vision-outer-div ${!language ? "v-o-d-arabic" : ""}`}>
          <p className={`vision-span ${!language ? "v-s-arabic" : ""}`}>
            {myLang.vision_header}
            <br />
            {myLang.vision_mission}
          </p>
          <p className={`vision-p ${!language ? "v-p-arabic" : ""}`}>
            {myLang.vision_content}
          </p>
        </div>
        <img
          className={`visionImg ${!language ? "v-i-arabic" : ""}`}
          src={visionImg}
          alt="visionImg"
        />
      </div>
      <div id="vision-div-2">
        <Quality
          imageSrc={circle1}
          span={myLang.vision_quality_1_head}
          para={myLang.vision_quality_1_content}
        />
        <Quality
          imageSrc={circle2}
          span={myLang.vision_quality_2_head}
          para={myLang.vision_quality_2_content}
        />
        <Quality
          imageSrc={circle3}
          span={myLang.vision_quality_3_head}
          para={myLang.vision_quality_3_content}
        />
        <Quality
          imageSrc={circle4}
          span={myLang.vision_quality_4_head}
          para={myLang.vision_quality_4_content}
        />
        <Quality
          imageSrc={circle5}
          span={myLang.vision_quality_5_head}
          para={myLang.vision_quality_5_content}
        />
      </div>
    </div>
  );
}

export default Vision;
