import { Outlet } from "react-router-dom";

// blocks
import Nav from "../components/blocks/Nav";
import Footer from "../components/blocks/Footer";

function Routeslayout() {
  return (
    <div className="root-layout">
      <Nav />

      <main>
        <Outlet />
      </main>

      <Footer />
    </div>
  );
}

export default Routeslayout;
