import React, { useContext } from "react";
import { MyLanguageContext } from "../context/LanguageContext";
import TrustedCompany from "./TrustedCompany";
import GoogleReviewCard from "./GoogleReviewCard";
import { GoogleReviewFullStar, GoogleReviewHalfStar } from "./GoogleReviewStar";
import enLang from "../assets/files/lang-en.json";
import arLang from "../assets/files/lang-ar.json";

// assets
import sriram from "../assets/images/Sriram-review.png";
import amirth from "../assets/images/amirth-review.png";
import azzan from "../assets/images/azzan-review.png";
import howthul from "../assets/images/Howthul-review.png";

function GoogleReview() {
  const { language } = useContext(MyLanguageContext);
  const myLang = language ? enLang : arLang;
  return (
    <div className="g-top">
      <div id="g-rating">
        <div id="g-overall-rating">
          <p id="g-rating-number">4.6</p>
          <div id="g-stars">
            <GoogleReviewFullStar starClassName="g-full-star" />
            <GoogleReviewFullStar starClassName="g-full-star" />
            <GoogleReviewFullStar starClassName="g-full-star" />
            <GoogleReviewFullStar starClassName="g-full-star" />
            <GoogleReviewHalfStar starClassName="g-half-star" />
          </div>
        </div>
        <div>
          <p id="g-customer-speak">{myLang.googleReview_speak}</p>
        </div>
      </div>

      <div id="g-reviews">
        <GoogleReviewCard
          reviewername={myLang.google_reviewer1Name}
          reviewerContent={myLang.google_reviewer1Content}
          starCount="4"
          imgSource={sriram}
          altSource="sriram"
          starClassName="g-reviewer-full-star"
        />

        <GoogleReviewCard
          reviewername={myLang.google_reviewer2Name}
          reviewerContent={myLang.google_reviewer2Content + "."}
          starCount="5"
          imgSource={amirth}
          altSource="amirth"
          starClassName="g-reviewer-full-star"
        />

        <GoogleReviewCard
          reviewername={myLang.google_reviewer3Name}
          reviewerContent={myLang.google_reviewer3Content}
          starCount="4"
          imgSource={azzan}
          altSource="azzan"
          starClassName="g-reviewer-full-star"
        />

        <GoogleReviewCard
          reviewername={myLang.google_reviewer4Name}
          reviewerContent={myLang.google_reviewer4Content}
          starCount="4"
          imgSource={howthul}
          altSource="howthul"
          starClassName="g-reviewer-full-star"
        />
      </div>

      <div>
        <div id="g-trust-separation">
          <hr className="g-line" />
          <p id="g-trusted-by">{myLang.google_reviewerCustomer}</p>
          <hr className="g-line" />
        </div>
        <TrustedCompany direction="right" />
        <TrustedCompany direction="left" />
      </div>
    </div>
  );
}

export default GoogleReview;
