import { useContext } from "react";
import { MyLanguageContext } from "../context/LanguageContext";

function Quality({ span, para, imageSrc }) {
  const { language } = useContext(MyLanguageContext);
  return (
    <div className={`quality ${!language ? "q-arabic" : ""}`}>
      <img src={imageSrc} alt="circle1" className="q-img" />
      <div className={`quality-description ${!language ? "q-d-arabic" : ""}`}>
        <span className="quality-span">{span}</span>
        <p className="quality-p">{para}</p>
      </div>
    </div>
  );
}

export default Quality;
