import { NavLink } from "react-router-dom";
import React, { useContext } from "react";
import { MyLanguageContext } from "../../context/LanguageContext";
import MuiToggleButton from "@mui/material/ToggleButton";
import { styled } from "@mui/material/styles";
import ToggleButtonGroup from "@mui/material/ToggleButtonGroup";
import phoneIcon from "../../assets/images/phone.svg";
import enLang from "../../assets/files/lang-en.json";
import arLang from "../../assets/files/lang-ar.json";
import enLogo from "../../assets/images/techsol-logo.svg";
import arLogo from "../../assets/images/techsol-logo-arabic.svg";

const ToggleButton = styled(MuiToggleButton)({
  "&.Mui-selected, &.Mui-selected:hover": {
    color: "#fff",
    backgroundColor: "#009AD6"
  }
});

function Nav() {
  const { language, toggle } = useContext(MyLanguageContext);
  const myLang = language ? enLang : arLang;
  const logo = language ? enLogo : arLogo;

  return (
    <nav className="root-nav">
      {/* <div className="logo" /> */}
      <img src={logo} className="logo" alt="logo" />
      <div className="general-options">
        <NavLink to="/" className="nav-link story">
          {myLang.nav_story}
        </NavLink>
        <NavLink to="contact" className="nav-link contact">
          {myLang.nav_contact}
        </NavLink>
        <div className="language-options">
          <ToggleButtonGroup
            size="small"
            value={language ? "en" : "ar"}
            exclusive
          >
            <ToggleButton
              id="togglBtn-1"
              className="language-link"
              value="en"
              onClick={toggle}
              style={{ borderRadius: "0.375rem" }}
              sx={{ "&:hover": { backgroundColor: "transparent" } }}
            >
              EN
            </ToggleButton>
            <ToggleButton
              id="togglBtn-2"
              className="language-link"
              value="ar"
              onClick={toggle}
              style={{ borderRadius: "0.375rem" }}
              sx={{ "&:hover": { backgroundColor: "transparent" } }}
            >
              AR
            </ToggleButton>
          </ToggleButtonGroup>
        </div>
        {/* <div className="line"/>
        <div className="phoneDetails">
          <img className="phoneIcon" src={phoneIcon} alt="phone" />
          <span className="phoneNumber">91775078</span>
        </div> */}
      </div>
    </nav>
  );
}

export default Nav;
