import React, { useContext } from "react";
import { MyLanguageContext } from "../context/LanguageContext";
import enLang from "../assets/files/lang-en.json";
import arLang from "../assets/files/lang-ar.json";

function ShopDetails() {
  const { language } = useContext(MyLanguageContext);
  const myLang = language ? enLang : arLang;

  const contactGridCssForArabic = `contacts-grid ${
    !language ? "c-g-arabic" : ""
  }`;
  const shopAddressCssForArabic = `p-tag shop-address ${
    !language ? "s-a-arabic" : ""
  }`;
  return (
    <div className="grid-3">
      <div className={contactGridCssForArabic}>
        <i className="fa-solid fa-phone fa-xl"></i>
        <p className="p-tag">91775078</p>
      </div>
      <div className={contactGridCssForArabic}>
        <i className="fa-solid fa-envelope fa-xl"></i>
        <p className="p-tag">sales@techsolme.com</p>
      </div>
      <div className={contactGridCssForArabic}>
        <i className="fa-solid fa-location-dot fa-xl"></i>
        <p className={shopAddressCssForArabic}>{myLang.shop_address}</p>
      </div>
    </div>
  );
}

export default ShopDetails;
