import { useState, useContext, useEffect, useRef } from "react";
import axios from "axios";
import { MyLanguageContext } from "../context/LanguageContext";
import enLang from "../assets/files/lang-en.json";
import arLang from "../assets/files/lang-ar.json";

// default values
const FORM_FIELDS = {
  name: "",
  phoneNumber: "",
  email: "",
  requirement: "",
};
const TIMEOUT_VALUE = 8000;

function CustomerForm() {
  const { language } = useContext(MyLanguageContext);
  const myLang = language ? enLang : arLang;

  const [formData, setFormData] = useState(FORM_FIELDS);
  const [submitted, setSubmitted] = useState(false);
  const timerId = useRef(null);

  // change state after 5 seconds
  useEffect(() => {
    if (submitted) {
      timerId.current = setTimeout(() => {
        setSubmitted(false);
      }, TIMEOUT_VALUE);
    }
    return () => {
      clearTimeout(timerId.current);
    };
  }, [submitted]);

  const onSubmithandler = (e) => {
    e.preventDefault();

    let headers = {
      "Content-Type": "application/json",
      "Access-Control-Allow-Origin": "*",
    };

    axios
      .post(`/api/customer/create-requirement`, formData, { headers })
      .then((res) => {
        console.log(" ~ file: CustomerForm.js:25 ~ .then ~ res:", res);
        setFormData(FORM_FIELDS);
        setSubmitted(true);
      })
      .catch((err) => {
        console.log(" ~ file: CustomerForm.js:29 ~ catch ~ err:", err);
      });
  };

  return (
    <div className="customer-form">
      <form className="customer-form" onSubmit={onSubmithandler}>
        <div className="customer-details">
          <label className="details-label" htmlFor="fullName">
            {myLang.form_name}
          </label>
          <input
            id="fullName"
            className="details-input"
            type="text"
            placeholder={myLang.formPlaceholder_name}
            onChange={(e) => setFormData({ ...formData, name: e.target.value })}
            value={formData.name}
            required
          />
        </div>
        <div className="customer-details">
          <label className="details-label" htmlFor="phoneNumber">
            {myLang.form_phoneNumber}
          </label>
          <input
            id="phoneNumber"
            className="details-input"
            type="number"
            placeholder={myLang.formPlaceholder_phoneNumber}
            onChange={(e) =>
              setFormData({ ...formData, phoneNumber: e.target.value })
            }
            value={formData.phoneNumber}
            required
          />
        </div>
        <div className="customer-details">
          <label className="details-label" htmlFor="email">
            {myLang.form_email}
          </label>
          <input
            id="email"
            className="details-input"
            type="email"
            placeholder={myLang.formPlaceholder_email}
            onChange={(e) =>
              setFormData({ ...formData, email: e.target.value })
            }
            value={formData.email}
          />
        </div>
        <div className="customer-details">
          <label className="details-label" htmlFor="requirementField">
            {myLang.form_requirement}
          </label>
          <textarea
            id="requirementField"
            className="details-inputTextArea"
            type="textArea"
            placeholder={myLang.formPlaceholder_requirement}
            onChange={(e) =>
              setFormData({ ...formData, requirement: e.target.value })
            }
            value={formData.requirement}
            required
            rows="6"
            cols="7"
          />
        </div>
        <button
          className={`submit-button ${submitted && "submitted-button"} `}
          type="submit"
        >
          {/* {submitted ? submittedText(myLang) : `${myLang.form_submit}`} */}
          {submitted ? `${myLang.form_submited}` : `${myLang.form_submit}`}
        </button>
      </form>
    </div>
  );
}

export default CustomerForm;
