import React, { useContext } from "react";
import { MyLanguageContext } from "../context/LanguageContext";
import enLang from "../assets/files/lang-en.json";
import arLang from "../assets/files/lang-ar.json";
// assets
import sales from "../assets/images/Sales-badge.svg";
import support from "../assets/images/Support-badge-2.svg";
import service from "../assets/images/Service-badge.svg";
import Provider from "./Provider";

function SolutionDetails() {
  const { language } = useContext(MyLanguageContext);
  const myLang = language ? enLang : arLang;
  // console.log(" ~ file: SolutionDetails.js:10 ~ myLang:", myLang);

  return (
    <div id="solution-details">
      <div id="s-tech-problem">
        <div id="g-head">
          <p id="g-h-text-1">
            {myLang.solutionDetails} 
            {myLang.solutionDetails_your}
            <span id="g-h-text-2">{myLang.solutionDetails_techProb}</span>
          </p>
        </div>
        <div id="g-description">
          <p id="g-d-text">{myLang.solutionDetails_content}</p>
        </div>
      </div>

      <div id="solution-container">
        <Provider
          source={sales.toString()}
          heading={myLang.providerSales_heading}
          content={myLang.providerSales_content}
        />

        <Provider
          source={service.toString()}
          heading={myLang.providerService_heading}
          content={myLang.providerService_content}
        />

        <Provider
          source={support.toString()}
          heading={myLang.providerSupport_heading}
          content={myLang.providerSupport_content}
        />
      </div>
    </div>
  );
}

export default SolutionDetails;
