import fullStar from "../assets/images/full-star.svg";
import halfStar from "../assets/images/half-star.svg";

export function GoogleReviewFullStar({ starClassName }) {
  return <img className={starClassName} src={fullStar} alt="fullStar" />;
}

export function GoogleReviewHalfStar({ starClassName }) {
  return <img className={starClassName} src={halfStar} alt="halfStar" />;
}
