import React, { useContext } from "react";
import { MyLanguageContext } from "../context/LanguageContext";
import enLang from "../assets/files/lang-en.json";
import arLang from "../assets/files/lang-ar.json";

function Locations() {
  const { language } = useContext(MyLanguageContext);
  const myLang = language ? enLang : arLang;
  return (
    <div id="l-div">
      <div className={`l-distribution ${!language ? "l-d-arabic" : ""}`}>
        <div className={`l-global-company ${!language ? "l-gl-c-arabic" : ""}`}>
          {myLang.globalCompany_head}
        </div>
        <div id="l-global-number">
          <div className={`l-describe ${!language ? "l-d-arabic" : ""}`}>
            <p className="l-digit">25</p>
            <p className="l-text">{myLang.globalCompany_yearInMarket}</p>
          </div>

          <div className={`l-describe ${!language ? "l-d-arabic" : ""}`}>
            <p className="l-digit">100K+</p>
            <p className="l-text">{myLang.globalCompany_happyCustomer}</p>
          </div>

          <div className={`l-describe ${!language ? "l-d-arabic" : ""}`}>
            <p className="l-digit">50+</p>
            <p className="l-text">{myLang.globalCompany_successfullProjects}</p>
          </div>

          <div className={`l-describe ${!language ? "l-d-arabic" : ""}`}>
            <p className="l-digit">6</p>
            <p className="l-text">{myLang.globalCompany_branches}</p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Locations;
