import { useContext } from "react";
import { MyLanguageContext } from "../context/LanguageContext";
import { GoogleReviewFullStar } from "./GoogleReviewStar";

function GoogleReviewCard({
  reviewername,
  starCount,
  reviewerContent,
  imgSource,
  altSource,
  starClassName,
}) {
  const { language } = useContext(MyLanguageContext);

  const createStar = (starCount) => {
    const stars = [];
    for (let i = 0; i < starCount; i++) {
      stars.push(
        <GoogleReviewFullStar key={i} starClassName={starClassName} />
      );
    }
    return <div className="g-reviewer-star-div">{stars}</div>;
  };
  return (
    <div className={`g-review-container ${!language ? "g-rv-c-arabic" : ""}`}>
      <div className={`g-reviewer-detail ${!language ? "g-rv-d-arabic" : ""}`}>
        <img src={imgSource} alt={altSource} className="g-review-image" />
        <div className={`g-reviewer-div ${!language ? "g-rvr-d-arabic" : ""}`}>
          <p className="g-reviewer-name">{reviewername}</p>
          {createStar(starCount)}
        </div>
      </div>
      <div
        className={`g-reviewer-content-layout ${
          !language ? "g-rv-c-l-arabic" : ""
        }`}
      >
        {reviewerContent}
      </div>
    </div>
  );
}

export default GoogleReviewCard;
